<template>
  <div id="app">
    <el-container>
      <el-header>

      </el-header>
      <el-main>
        <el-input v-model="target" placeholder="目标价格" @input="refresh"> 
          <template slot="prepend">目标价格：</template>
        </el-input>
        <el-input v-model="bonus" placeholder="送券价格"  @input="refresh">
          <template slot="prepend">送券价格：</template>
        </el-input>
        <el-input v-model="discountLimit" placeholder="满减门槛"  @input="refresh">
          <template slot="prepend">满减门槛：</template>
        </el-input>
        <el-input v-model="discount" placeholder="满减额度"  @input="refresh">
          <template slot="prepend">满减额度：</template>
        </el-input>
        <el-card class="box-card">
      <div class="text item" style="text-align: left;">最优惠价格{{cost}}元，使用满减券{{num}}张。</div>
    </el-card>
        <el-table
      :data="tableData"
      border
      :row-class-name="tableRowClassName"
      style="width: 100%"
    >
      <el-table-column prop="first_cost" label="第一次花费" width="200"></el-table-column>
      <el-table-column prop="num" label="拿到满减券张数" width="200">
      </el-table-column>
      <el-table-column prop="second_cost" label="第二次花费" width="200"></el-table-column>
      <el-table-column prop="use" label="使用满减券张数" width="200"></el-table-column>
      <el-table-column prop="cost" label="共花费" width="200"></el-table-column>
      
    </el-table>
      </el-main>
    </el-container>
  </div>
</template>

<script>

export default {
  name: "Main",
  data() {
    return {
      tableData: [],
      target: 20000,
      bonus: 999,
      discountLimit: 300,
      discount: 80,
      cost: 0,
      num: 0
    };
  },
  mounted() {
     this.refresh();
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
      if (row["min"]) {
        return "warning-row";
      }
      return "";
    },
    refresh() {
      this.tableData = [];
      let discount_cost = this.discountLimit - this.discount;
     let min = Number.MAX_VALUE;
     let bestNum = 0;
      for (let num = 0;num * this.bonus <= this.target;num++) {
          let remain = this.target - this.bonus * num;
          let discountNum = Math.floor(remain / this.discountLimit);
          
          if (discountNum > num) {
              discountNum = num;
          }

          remain = remain - discountNum * this.discountLimit;
          let cost = this.bonus * num + discount_cost * discountNum + remain;
          if (cost < min) {
              min = cost;
              bestNum = num;
          }
        
          this.tableData.push({
            first_cost: num + '*' + this.bonus + '='  + (num * this.bonus),
            num: num,
            second_cost: (this.target - this.bonus * num) + '-' + this.discount + '*'  +discountNum+ '=' + (discount_cost * discountNum + remain),
            use: discountNum,
            cost: cost,
            min: false
          });
      }
      this.cost = this.tableData[bestNum].cost;
      this.num = this.tableData[bestNum].use;
      this.tableData[bestNum].min = true;
      console.log(this.tableData)

    }
  }
  
  
};
</script>

